@import 'main';
// End Of Import

.navover{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .8);
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideNav{
    padding: 24px;
    position: fixed;
    top: 0;
    right: -100%;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 9999;
    overflow: auto;
    transition: .5s;

    &.open {
        right: 0;
    }

    .opt{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang{


            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                color: #000;
                font-size: 15px;
            }
        }

        .xtr{
            position: relative;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 10px;

            .srch{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                svg{

                    path{
                        fill: #000;
                    }
                }
            }

            form{
                opacity: 0;
                visibility: hidden;
                z-index: 99;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0, .8);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                display: none;

                @include Sm{
                    display: flex;
                }

                &.open{
                    opacity: 1;
                    visibility: visible;

                    button{
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-45%);
                        margin-left: 10px;
                        width: 30px;
                        height: 30px;

                        svg{
                            margin-top: 5px;
                        }
                    }

                    input{
                        display: block;
                    }
                }

                .icon{
                    position: absolute;
                    top: 15%;

                    svg{

                        path{
                            fill: #fff;
                        }
                    }
                }

                .cont{
                    position: relative;
                    top: -15%;
                }

                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;

                    svg{


                        path{
                            fill: #000;
                        }
                    }
                }

                input{
                    border: 0;
                    border-radius: 5px;
                    width: 600px;
                    height: 45px;
                    padding: 0 25px;
                    background-color: $color-white;
                    display: none;

                    @include Xs{
                        width: 400px;
                    }

                    @include mob375{
                        width: 290px;
                    }
                }
            }
        }

        .close1{
            font-size: 20px;
            color: #84848445;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links{
        display: block;

        .list{
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;

            & > li{
                padding: 7px 15px;
                width: 100%;

                a{
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: $color-black;
                    line-height: 1.6;
                    transition: .5s;                

                    &:hover{
                        color: #919191;
                    }
                }

                &:not(:last-child){
                    border-bottom: 1px solid #dedede;
                }
            }
        }
    }
}

// Side Nav

// Header
header{
    position: relative;
    min-height: 100vh;
    overflow-y: hidden;

    @include Xs{
        min-height: 80vh;
    }

    @include mob480{
        min-height: 70vh;
    }

    @include mob375{
        min-height: 60vh;
    }

    & > img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;

        h1{
            font-size: 50px;
            color: #FFF;
            line-height: 1.5;
            text-align: center;

            @include Xs{
                font-size: 40px;
            }

            @include mob480{
                font-size: 30px;
            }

            @include mob375{
                font-size: 25px;
            }
        }
    }

    .bottom{
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.149);
        position: absolute;
        bottom: 60px;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;

        @include mob480{
            bottom: 30px;
        }

        &:after{
            content: "";
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(255, 255, 255, 0.149);
            animation: pulse 1s infinite linear;
        }
    }
}

nav{
    margin-top: 33px;
    transition: .5s;
    
    .content-top{
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    .logo{
        margin-left: auto;
    }

    .links{
        transition: .5s;

        @include Sm{
            display: none;
        }

        &.open{
            transform: translateX(200px);
            opacity: 0;
            visibility: hidden;
        }

        ul{

            display: flex;


            li{


                a{
                    font-size: 17px;
                    font-family: "frtBold";
                    color: rgb(255, 255, 255);
                    transition: .5s;

                    @include Md{
                        font-size: 15px;
                    }

                    @include Lg{
                        font-size: 17px;
                    }

                    &:hover{
                        color: #bdbdbd;
                    }
                }

                &:not(:last-child){
                    margin-left: 30px;

                    @include Md{
                        margin-left: 20px;
                    }

                    @include Lg{
                        margin-left: 30px;
                    }
                }
            }
        }
    }

    .search{
        margin-right: 45px;

        @include Sm{
            display: none;
        }

        .over{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 99;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        form{
            position: relative;

            & > button{
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
                transition: .5s;


                &.hide{
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }

        .serach-content{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 200px;
            transition: .5s;

            &.open{
                right: calc(-200px - -31px);
                z-index: 99;
            }

            input{
                width: 200px;
                height: 30px;
                background-color: transparent;
                border: 0;
                border-bottom: 1px solid #ccc;
                color: #fff;

                &::placeholder{
                    color: rgb(212, 212, 212);
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        margin-bottom: 7px;
        cursor: pointer;
        display: none;

        @include Sm{
            display: flex;
        }

        span{
            width: 100%;
            height: 2px;
            background-color: #fff;
            position: relative;
            display: block;

            &:after,&:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: #fff;
            }

            &:before {
                top: 8px;
            }

            &:after {
                top: -8px;
            }
        }
    }

    &.scroll{
        padding: 30px 0;
        margin-top: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background-color: #FFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        transition: .5s;

        @include mob480{
            padding: 15px 0;
        }

        .logo{
            img{
                filter: brightness(0);
            }
        }

        .links{

            li{

                a{
                    color: #000;
                }
            }
        }

        .search{

            svg{

                path{
                    fill: #000;
                }
            }

            input{
                color: #000 !important;
            }
        }

        .bars{

            span{
                background-color: #000;


                &:after,&:before{
                    background-color: #000;
                }
            }
        }
    }
}

.main{
    position: relative;
    z-index: 9;
}

// Start Of Overview

.about{
    padding: 70px 0;

    @include Sm{
        padding: 50px 0;
    }

    .rtContent{
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding-right: 15px;

        @include Sm{
            text-align: center;
        }

        @include Xs{
            padding: 0;
        }

        h2{
            font-size: 24px;
            font-family: "frtBlk";
            line-height: 1.787;
            color: #000;
            margin-bottom: 30px;
        }

        img{
            display: none;

            @include Sm{
                display: block;
                width: max-content;
                margin: auto;
            }
        }

        p{
            margin-bottom: 30px;
            color: #3f3f3f;
            line-height: 1.765;
            font-size: 17px;
            font-family: "frtLight";              

            @include Sm{
                margin-top: 25px;
            }

            &:first-child{
                font-size: 20px;
                font-family: 'frtLight';
                margin-bottom: 25px;
                padding-right: 20px;
                line-height: 1.2;
                position: relative;
                width: max-content;

                @include Sm{
                    margin: auto;
                    margin-bottom: 25px;
                    width: max-content;
                }


                &:after{
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 1px;
                    height: 100%;
                    background-color: #c0c0c0;
                }
            }
        }

        a{
            font-size: 16px;
            font-family: "frtLight";
            color: #b5b5b5;
            width: max-content;
            text-decoration: none;
            transition: .5s;

            @include Sm{
                display: block;
                margin: auto;
            }

            &:hover{
                color: rgb(81, 81, 81);
            }
        }
    }

    .ltContent{

        img{
            width: 100%;

            @include Sm{
                display: none;
            }
        }
    }
}

// End Of Overview

// Start Companies

.companies{
    padding: 80px 0;
    background-image: url("../images/clients.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include Xs{
        padding: 40px 0;
    }


    .title{
        text-align: center;
        color: #000;

        p{
            font-size: 20px;
            font-family: "frtLight";
            position: relative;
            width: max-content;
            margin: auto;
            margin-bottom: 15px;

            &:after,&:before{
                content: "";
                position: absolute;
                top: 65%;
                display: block;
                width: 20px;
                height: 1px;
                background-color: #c0c0c0;
            }

            &:after{
                right: -40px;
            }

            &:before{
                left: -40px;
            }
        }

        h2{
            font-size: 24px;
            font-family: "frtBlk";
            margin-bottom: 35px;
        }
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 685px;
        margin: auto;

        @include Xs{
            width: 100%;
        }

        @include mob480{
            justify-content: center;
        }


        .company{
            width: 205px;
            height: 240px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-image: url("../images/shape.png");
            background-position: center;
            background-size: cover;

            @include Xs{
                margin: auto;
            }

            img{
                transition: .5s;
            }

            &.bottom{
                margin-top: 20px;

                @include Xs{
                    margin: auto;
                }
            }

            a{
                margin-top: -30px;
            }

            &:hover{

                img{
                    transform: scale(1.02);
                }
            }
        }
    }

    .owl-dots{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        bottom: -20px;

        .owl-dot{

            span{
                display: inline-block;
                width: 10px;
                height: 10px;
                background-color: rgba(0,0,0, .4);
                margin: 2.5px;
            }

            &.active{


                span{
                    background-color: rgba(0,0,0, 1);
                }
            }
        }
    }
}

// End Companies

// Start Clients

.clients{
    padding: 100px 0 90px;

    @include Sm{
        padding-bottom: 50px;
    }

    @include Xs{
        padding: 50px 0;
    }

    .title{
        display: flex;
        justify-content: space-between;
        // align-items: center;
        flex-wrap: wrap;

        & > p{
            font-size: 20px;
            font-family: 'frtLight';
            margin-bottom: 25px;
            padding-right: 20px;
            line-height: 1.2;
            position: relative;
            color: #000;
            line-height: 1.2;

            @include mob375{
                font-size: 25px;
            }

            &:after{
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                width: 1px;
                height: 100%;
                background-color: #c0c0c0;
            }
        }

        a{
            font-size: 16px;
            color: #b5b5b5;
            line-height: 1.2;
            font-family: "frtLight";
            transition: .5s;

            &:hover{
                color: #515151;
            }

            @include mob480{
                margin-top: 5px;
            }
        }

        h2{
            width: 100%;

            p{
                font-size: 24px;
                color: rgb(81, 81, 81);
                line-height: 1.889;
                font-family: "frtBlk";
                color: #000;
                width: 65%;
                line-height: 1.8;
    
                @include Xs{
                    width: 100%;
                }

                @include mob375{
                    font-size: 16px;
                }
            }
        }
    }

    .owl-carousel {
        position: relative;

        .owl-stage{
            position: relative;
        }
        
        .owl-item img {
            width: initial;
            display: block;
            margin: auto;
        }

        .item {
            display: flex;
            align-items: center;
            justify-content: center;

            a{
                position: relative;
                width: 165px;
                height: 166px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
    
                &:hover{
    
                    img{
                        opacity: 1;
                        filter: unset;
                    }
    
                    p{
                        top: 0;
                        opacity: 1;
                        visibility: visible;
                    }
                }
    
                img{
                    opacity: .6;
                    filter: grayscale(100%);
                    transition: .5s;
                }
    
                p{
                    background-color: $color-black;
                    position: absolute;
                    top: 10px;
                    right: 50%;
                    transform: translateX(50%);
                    font-size: 13px;
                    color: rgb(255, 255, 255);
                    line-height: 1.2;
                    font-family: "frtlight";
 
                    padding: 3px 4px 6px;
                    min-width: 91px;         
                    border-radius: 3px;
                    transition: .5s;
                    text-align: center;
                    opacity: 0;
                    visibility: hidden;
                    
                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 5px;
                        border-color: #000 transparent transparent;
                        border-style: solid;
                        top: 100%;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }
                }
            }
        }

        .owl-nav {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            margin-top: 0;
            width: 104%;
            display: flex;
            justify-content: space-between;
            font-size: 40px;
            height: 0;

            @include Md{
                width: 97%;
            }

            @include Lg{
                width: 104%;
            }

            @include Sm{
                display: none;
            }

            button {
                margin-top: 0;
                color: #d8d8d8;
                transition: .5s;

                &:hover {
                    
                    span{
                        border: 1px solid #000;
                    }
                }

                &.owl-prev {
                    margin-right: -25px;

                    span{
                        border-top: 0;
                        border-left: 0;
                        transform: rotate(-45deg);
                    }
                }

                &.owl-next {
                    margin-left: -25px;

                    span{
                        border-bottom: 0;
                        border-right: 0;
                        transform: rotate(-45deg);
                    }
                }

                span{
                    display: block;
                    width: 15px;
                    height: 15px;
                    border: 1px solid #adadad;
                    transition: .5s;
                }
            }
        }

        .owl-dots{
            display: flex;
            justify-content: center;
            align-items: center;
            display: none;

            @include Sm{
                display: flex;
            }

            .owl-dot{

                span{
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    background-color: rgba(0,0,0, .4);
                    margin: 2.5px;
                }

                &.active{


                    span{
                        background-color: rgba(0,0,0, 1);
                    }
                }
            }
        }
    }
}

// End Of Clients

// Start Footer

footer{
    background-color: #000;

    & > .container{
        padding: 50px 0;
        border-bottom: 1px solid #1a1a1a;

        @include mob480{
            padding: 30px 0;
        }
    }

    .content{
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
            justify-content: center;
        }
    }

    .details{
        display: flex;
        margin-right: auto;
        color: $color-white;

        @include Sm{
            margin-right: unset;
            margin-top: 35px;
        }

        @include Xs{
            flex-direction: column;
            align-items: center;
            text-align: center;
        }

        i{
            font-size: 15px;
            margin-left: 10px;
        }

        h3{
            font-size: 17px;
            font-family: "frtBlk";

            & + p{
                margin-bottom: 0;
                font-size: 15px;
                font-family: "frtLight";
            }
        }

        a{
            font-size: 15px;
            font-family: "frtLight";
            margin-bottom: 0;
            color: currentColor;
        }
    }

    .detail{

        &:not(:last-child){
            margin-left: 60px;

            @include Xs{
                margin-left: 0;
                margin-bottom: 25px;
                text-align: center;
            }
        }
    }
}

// End Of Footer

